import { Component, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core';
import {
  FormBuilder,
  FormGroup,
  FormArray,
  Validators,
  FormControl,
} from '@angular/forms';

import { AuthService } from '../core/auth.service';
import { LanguageService } from '../core/language.service';
import { UtilityService } from '../core/utility.service';
import { list } from './../signup/countryCode';
import { MustMatch } from 'src/app/shared/directives/must-match.validator';
import { MenuController } from '@ionic/angular';
import { ServiceProviderService } from '../core/service-provider.service';
@Component({
  selector: 'app-service-provider-signup',
  templateUrl: './service-provider-signup.page.html',
  styleUrls: ['./service-provider-signup.page.scss'],
})
export class ServiceProviderSignupPage implements OnInit {
  registerForm: FormGroup;
  selectedCar: number;
  model: any = {};
  countryList = list;
  services: any;
  servicecat: any = [];
  languages;
  constructor(
    private authService: AuthService,
    private utility: UtilityService,
    private languageService: LanguageService,
    private fb: FormBuilder,
    private menuCtrl: MenuController,
    private providerService: ServiceProviderService
  ) {
    this.buildForm();
  }

  getServiceCategoriesList() {
    this.providerService.serviceCatList().subscribe(
      (res) => {
        console.log(res);
        this.servicecat = res;
        this.utility.dismiss();
      },
      (err) => {
        console.log(err);
        this.utility.dismiss();
        this.utility.showAlert('Something went wrong!');
      }
    );
  }

  ngOnInit() {
    console.log('ngOnit');
    this.menuCtrl.enable(false);

    this.languages = this.languageService.languages;
  }
  ionViewWillEnter() {
    this.menuCtrl.swipeGesture(false);
    this.getServiceCategoriesList();
  }

  buildForm() {
    this.registerForm = this.fb.group(
      {
        firstname: [
          '',
          [Validators.required, Validators.pattern(/^[a-zA-Z]+$/)],
        ],
        lastname: [
          '',
          [Validators.required, Validators.pattern(/^[a-zA-Z]+$/)],
        ],
        // gender: [''/*, [Validators.required]*/],

        // fiscalNumber: [''/*, [Validators.required, Validators.pattern(/^\d+$/)]*/],
        address: ['', [Validators.required]],
        country: ['', [Validators.required]],
        city: ['', [Validators.required, Validators.pattern(/^[a-zA-Z]+$/)]],
        zipCode: ['', [Validators.required]],
        services_given: ['' /*, [Validators.required]*/],
        email: [
          '',
          [
            Validators.required,
            Validators.pattern(/[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,4}$/),
          ],
        ],
        password: ['', [Validators.required, Validators.minLength(6)]],
        cPassword: ['', [Validators.required, Validators.minLength(6)]],
        numberOfPet: [''],
        petType: new FormArray([]),
      },
      {
        validator: MustMatch('password', 'cPassword'),
      }
    );
  }
  get petType() {
    return this.registerForm.get('petType') as FormArray;
  }
  // pattern=""

  get f() {
    return this.registerForm.controls;
  }

  addPetType() {
    const pet = this.fb.group({
      petType: ['', [Validators.required]],
    });
    this.petType.push(new FormControl('', Validators.required));
  }

  removePetType(i) {
    this.petType.removeAt(i);
  }

  handleChange(e) {
    console.log(e);
    const val = e.detail.value;
    this.petType.clear();
    for (let i = 1; i <= val; i++) {
      this.addPetType();
    }
  }
  submit(form) {
    console.log('here');
    Object.keys(form.controls).forEach((key) => {
      form.get(key).markAsTouched();
    });
    if (form.status.toLowerCase() === 'valid') {
      const formObject = form.value;
      formObject.role = 'service provider';

      delete formObject.cPassword;
      this.utility.setLocal('signUpData', JSON.stringify(formObject));
      this.utility.navigate('/service-provider-signup2');
    }
  }

  switchLanguage(e) {
    console.log(e);
    const language = e.toLowerCase();
    this.languageService.useLanguage(language.code);
  }

  ngOnDestroy() {
    console.log('working');
    this.menuCtrl.enable(true);
  }
  ionViewWillLeave() {
    console.log('leaving');
    this.menuCtrl.enable(true);
    this.menuCtrl.swipeGesture(true);
  }

  getServiceCategory() {
    this.authService.getServiceCategory().subscribe((res) => {
      this.services = res['data'];
      console.log(this.services);
    });
  }

  capitalizeFirstLetter(string) {
    return string.charAt(0).toUpperCase() + string.slice(1);
  }
}
